/* === assets/css/components/brand-header.css === */
/* ==========================================================================
   Brand Header - Bold Professional Design
   Green Bier Sport Ventures Dashboard
   ========================================================================== */

.brand-header-inline {
    display: flex;
    align-items: flex-end; /* Align to bottom edge to match KPI tiles and date toggles */
    gap: 8px;
    flex: 0 0 auto;
    min-width: 220px;
    position: relative;
    z-index: 200;
    height: var(--dashboard-topline-height, 76px);
    margin-right: 0;
    padding: 0; /* No extra padding - align logo with filter chips and table */
}

/* Bear Shield Logo - Sharp edges with subtle glow, matches KPI tile height */
.brand-logo-inline {
    display: block;
    height: var(--dashboard-topline-height, 76px); /* Match KPI tile height exactly */
    width: auto;
    min-width: var(--dashboard-topline-height, 76px);
    object-fit: contain;
    /* Subtle definition without pixelation */
    filter: 
        contrast(1.05) 
        brightness(1.02)
        drop-shadow(0 1px 2px rgba(0, 0, 0, 0.8))
        drop-shadow(0 2px 4px rgba(0, 0, 0, 0.6))
        drop-shadow(0 0 8px rgba(0, 143, 88, 0.4))
        drop-shadow(0 0 12px rgba(var(--color-primary-bright-rgb), 0.3));
    flex-shrink: 0;
    /* Subtle static glow pulse (no movement) */
    animation: logo-static-glow 4s ease-in-out infinite;
}

@keyframes logo-static-glow {
    0%, 100% {
        filter: 
            contrast(1.05) 
            brightness(1.02)
            drop-shadow(0 1px 2px rgba(0, 0, 0, 0.8))
            drop-shadow(0 2px 4px rgba(0, 0, 0, 0.6))
            drop-shadow(0 0 8px rgba(0, 143, 88, 0.4))
            drop-shadow(0 0 12px rgba(var(--color-primary-bright-rgb), 0.3));
    }
    50% {
        filter: 
            contrast(1.08) 
            brightness(1.04)
            drop-shadow(0 1px 3px rgba(0, 0, 0, 0.85))
            drop-shadow(0 2px 5px rgba(0, 0, 0, 0.65))
            drop-shadow(0 0 10px rgba(0, 143, 88, 0.5))
            drop-shadow(0 0 14px rgba(var(--color-primary-bright-rgb), 0.4));
    }
}

/* Brand Content Container - Fill entire height to align with logo and tiles */
.brand-content-inline {
    display: flex;
    flex-direction: column;
    justify-content: flex-end; /* Align content to bottom edge */
    align-items: flex-start;
    height: 100%;
    flex: 1;
    min-height: var(--dashboard-topline-height, 76px); /* Ensure it fills the full height */
}

/* Brand text fills container - sleek elegant stack with tight spacing */
/* GREEN BIER on top, SPORT VENTURES below - aligned to top and bottom edges */
.brand-text-stacked {
    display: flex;
    flex-direction: column;
    justify-content: space-between; /* Push GREEN BIER to top, SPORT VENTURES to bottom */
    gap: 0; /* No gap - space-between handles spacing */
    align-items: flex-start;
    min-width: clamp(200px, 12vw, 320px);
    width: 100%; /* Allow full width so SPORT VENTURES can extend */
    height: 100%; /* Fill full height to align with container */
    padding: 0;
}

/* Primary Brand Name - Blockier Geometric Typography */
/* LINE 1: GREEN BIER - Stacked on top of SPORT VENTURES */
.brand-line-1 {
    font-family: 'Cormorant Garamond', Georgia, serif; /* Maintain Garamond font */
    font-weight: 900; /* Bold for emphasis */
    font-size: calc(var(--dashboard-topline-height, 76px) * 0.62); /* Larger to fill more vertical space */
    letter-spacing: 0.02em; /* Closer letter spacing */
    text-transform: uppercase;
    text-align: left;
    margin: 0;
    padding: 0;
    display: block;
    order: 1; /* Explicitly ensure GREEN BIER is first (on top) */
    width: fit-content; /* Only take up needed width so SPORT VENTURES can extend past */
    max-width: 100%;
    /* Metallic Emerald Gradient with moving shimmer highlight */
    background: 
        linear-gradient(
            110deg,
            transparent 0%,
            transparent 25%,
            rgba(200, 255, 230, 0.5) 35%,
            rgba(240, 255, 250, 0.7) 40%,
            rgba(200, 255, 230, 0.5) 45%,
            transparent 55%,
            transparent 100%
        ),
        linear-gradient(
            135deg, 
            #007a4d 0%, 
            #00b06c 20%,
            #00d689 35%,
            #007a4d 50%, 
            #00b06c 65%,
            #008f58 80%,
            #005a38 100%
        );
    background-size: 400% 100%, 100% 100%;
    background-position: -200% 0, 0 0;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    /* Enhanced shadows for better readability - stronger presence */
    filter: drop-shadow(0 2px 10px rgba(0, 0, 0, 0.85))
            drop-shadow(0 4px 12px rgba(0, 0, 0, 0.6))
            drop-shadow(0 0 16px rgba(0, 143, 88, 0.5))
            drop-shadow(0 0 24px rgba(var(--color-primary-bright-rgb), 0.35));
    line-height: 1;
    /* Slow moving metallic shimmer */
    animation: text-metallic-shimmer 18s linear infinite;
}

@keyframes text-metallic-shimmer {
    0% {
        background-position: -200% 0, 0 0;
    }
    100% {
        background-position: 200% 0, 0 0;
    }
}

/* Secondary Brand Text - Refined Subtitle */
/* LINE 2: SPORT VENTURES - Stacked below GREEN BIER, extends past GREEN BIER */
.brand-line-2 {
    font-family: 'Cormorant Garamond', Georgia, serif;
    font-weight: 500; /* Reduced from 600 to make GREEN BIER more dominant */
    font-size: calc(var(--dashboard-topline-height, 76px) * 0.40); /* Increased to reduce gap with GREEN BIER */
    letter-spacing: 0.35em; /* Increased spacing to extend past GREEN BIER */
    text-transform: uppercase;
    text-align: left;
    order: 2; /* Explicitly ensure SPORT VENTURES is second (below GREEN BIER) */
    width: 100%; /* Take full width to extend past GREEN BIER */
    min-width: 100%; /* Ensure it extends */
    align-self: stretch; /* Stretch to full width of container */
    /* Metallic silver-blue gradient with moving shimmer */
    background: 
        linear-gradient(
            110deg,
            transparent 0%,
            transparent 25%,
            rgba(230, 245, 255, 0.6) 35%,
            rgba(255, 255, 255, 0.8) 40%,
            rgba(230, 245, 255, 0.6) 45%,
            transparent 55%,
            transparent 100%
        ),
        linear-gradient(
            135deg,
            rgba(190, 205, 215, 0.95) 0%,
            rgba(210, 225, 240, 0.95) 30%,
            rgba(220, 235, 250, 1) 50%,
            rgba(210, 225, 240, 0.95) 70%,
            rgba(190, 205, 215, 0.95) 100%
        );
    background-size: 400% 100%, 100% 100%;
    background-position: -200% 0, 0 0;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    /* Enhanced text shadows for better readability - stronger presence */
    filter: drop-shadow(0 1px 5px rgba(0, 0, 0, 0.85))
            drop-shadow(0 2px 10px rgba(0, 0, 0, 0.6))
            drop-shadow(0 0 16px rgba(0, 188, 212, 0.3))
            drop-shadow(0 0 24px rgba(var(--color-primary-bright-rgb), 0.25));
    line-height: 1;
    display: inline-flex;
    align-items: flex-end;
    column-gap: clamp(0.35em, 0.45em, 0.55em);
    margin: 0; /* No margin needed - parent handles bottom alignment */
    padding: 0;
    white-space: nowrap;
    /* Slow moving metallic shimmer - synchronized with line 1 */
    animation: text-metallic-shimmer 18s linear infinite;
    animation-delay: 0.5s; /* Slight offset for visual interest */
}

.brand-line-2 .brand-word {
    letter-spacing: inherit;
    display: inline-flex;
}

/* Alternative color variants */
.brand-line-2.brand-green {
    background: 
        linear-gradient(
            110deg,
            transparent 0%,
            transparent 25%,
            rgba(180, 255, 220, 0.5) 35%,
            rgba(220, 255, 240, 0.7) 40%,
            rgba(180, 255, 220, 0.5) 45%,
            transparent 55%,
            transparent 100%
        ),
        linear-gradient(
            135deg,
            rgba(0, 143, 88, 0.9) 0%,
            rgba(var(--color-primary-bright-rgb), 0.9) 50%,
            rgba(0, 143, 88, 0.9) 100%
        );
    background-size: 400% 100%, 100% 100%;
    background-position: -200% 0, 0 0;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    animation: text-metallic-shimmer 18s linear infinite;
    animation-delay: 0.5s;
}

.brand-line-2.brand-dark {
    background: 
        linear-gradient(
            110deg,
            transparent 0%,
            transparent 25%,
            rgba(200, 210, 220, 0.5) 35%,
            rgba(220, 230, 240, 0.7) 40%,
            rgba(200, 210, 220, 0.5) 45%,
            transparent 55%,
            transparent 100%
        ),
        linear-gradient(
            135deg,
            rgba(200, 210, 220, 0.7) 0%,
            rgba(180, 190, 200, 0.7) 50%,
            rgba(200, 210, 220, 0.7) 100%
        );
    background-size: 400% 100%, 100% 100%;
    background-position: -200% 0, 0 0;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    animation: text-metallic-shimmer 18s linear infinite;
    animation-delay: 0.5s;
}

/* ===== Responsive adjustments ===== */
@media (max-width: 1400px) {
    .brand-header-inline {
        min-width: 240px;
    }
}

@media (max-width: 1200px) {
    .brand-header-inline {
        min-width: 220px;
    }
    
    .brand-line-1 {
        font-size: calc(var(--dashboard-topline-height, 76px) * 0.58);
        letter-spacing: 0.02em; /* Maintain blocky spacing */
    }
    
    .brand-line-2 {
        font-size: calc(var(--dashboard-topline-height, 76px) * 0.26);
        letter-spacing: 0.40em; /* Extended spacing to go past GREEN BIER */
        column-gap: 0.40em;
    }
}

@media (max-width: 992px) {
    .brand-header-inline {
        min-width: 200px;
        --brand-line1-scale: 0.35;
        --brand-line2-scale: 0.18;
    }
    
    .brand-line-1 {
        font-size: calc(var(--dashboard-topline-height, 76px) * 0.52);
        letter-spacing: 0.02em; /* Maintain blocky spacing */
    }
    
    .brand-line-2 {
        font-size: calc(var(--dashboard-topline-height, 76px) * 0.22);
        letter-spacing: 0.35em; /* Extended spacing to go past GREEN BIER */
        column-gap: 0.35em;
    }
}

@media (max-width: 768px) {
    .brand-header-inline {
        min-width: 180px;
        --brand-line1-scale: 0.34;
        --brand-line2-scale: 0.16;
    }
    
    .brand-line-1 {
        font-size: calc(var(--dashboard-topline-height, 76px) * 0.50);
        letter-spacing: 0.02em; /* Maintain blocky spacing */
    }
    
    .brand-line-2 {
        font-size: calc(var(--dashboard-topline-height, 76px) * 0.20);
        letter-spacing: 0.30em; /* Extended spacing to go past GREEN BIER */
        column-gap: 0.30em;
    }
}


/* === assets/css/pages/fetch-picks.css === */
/* ==========================================================================
   FETCH PICKS PAGE - CUSTOM STYLES
   ==========================================================================
   Lightweight model-picks viewer. Mirrors the weekly-lineup look & feel
   but with a simplified 7-column table (no Track column) and no Cosmos sync.
   ========================================================================== */

/* ===== TOOLBAR LAYOUT ===== */
body.page-fetch-picks .toolbar-layout {
  margin-left: 0.75in;
  margin-right: 0.75in;
  width: calc(100% - 1.5in);
  box-sizing: border-box;
}

@media (max-width: 900px) {
  body.page-fetch-picks .toolbar-layout {
    margin-left: 0.5in;
    margin-right: 0.5in;
    width: calc(100% - 1in);
  }
}

@media (max-width: 600px) {
  body.page-fetch-picks .toolbar-layout {
    margin-left: 0.25in;
    margin-right: 0.25in;
    width: calc(100% - 0.5in);
  }
}

/* ===== PAGE SPECIFIC ===== */
body.page-fetch-picks {
  --fp-accent-primary: var(--emerald-500);
  --fp-accent-secondary: var(--gold-400);
  --fp-surface: rgba(8, 16, 28, 0.75);
  --fp-border: rgba(80, 100, 130, 0.25);
  --fp-text: #e6f2f6;
  --dashboard-topline-height: 91px;
  --table-font: var(--font-body);

  /* Zebra Stripes */
  --zebra-odd: rgba(4, 12, 24, 0.15);
  --zebra-even: rgba(30, 55, 85, 0.08);

  background-color: rgba(3, 11, 22, 0.18);
  background-image: url("../assets/JB_Bearish_Market_FULL_page.png");
  background-size: cover;
  background-position: center top;
  background-repeat: no-repeat;
  background-attachment: fixed;
  position: relative;
  isolation: isolate;
}

/* ===== OVERLAY EFFECTS ===== */
body.page-fetch-picks::before {
  content: "";
  position: fixed;
  inset: 0;
  z-index: -1;
  background: linear-gradient(
    180deg,
    rgba(4, 10, 20, 0.12) 0%,
    rgba(4, 10, 20, 0.1) 15%,
    rgba(4, 10, 20, 0.08) 30%,
    rgba(4, 10, 20, 0.14) 50%,
    rgba(4, 10, 20, 0.18) 75%,
    rgba(4, 10, 20, 0.25) 100%
  );
  pointer-events: none;
  opacity: 0.2;
}

body.page-fetch-picks::after {
  content: "";
  position: fixed;
  inset: 0;
  z-index: -2;
  background: rgba(3, 11, 22, 0.75);
  mix-blend-mode: multiply;
  pointer-events: none;
}

/* Nav styling consolidated in navigation.css (core.min.css) */

/* ==========================================================================
   LAYOUT SHELL
   ========================================================================== */
body.page-fetch-picks .main-container {
  padding: 0 0 20px;
  margin-top: 12px;
}

body.page-fetch-picks .main-dashboard-layout { gap: 8px; }

/* ===== TOPLINE ===== */
body.page-fetch-picks .dashboard-topline {
  background: transparent;
  border: none;
  box-shadow: none;
  border-radius: 0;
  padding: 6px 0.5in 0;
  margin-bottom: 8px;
}

body.page-fetch-picks .brand-header-inline,
body.page-fetch-picks .brand-header {
  background: transparent;
  border: none;
  box-shadow: none;
  border-radius: 0;
}

/* ==========================================================================
   TABLE CONTAINER
   ========================================================================== */
body.page-fetch-picks .table-container,
.page-fetch-picks .fetch-picks-table-wrapper {
  margin-top: 0;
  margin-left: 0.5in;
  margin-right: 0.5in;
  width: calc(100% - 1in);
  box-sizing: border-box;
  position: relative;
  z-index: 1;
  overflow: visible !important;
  background: transparent !important;
  border: none !important;
  box-shadow: none !important;
  border-radius: 0 !important;
  padding: 0;
  contain: none !important;
}

.page-fetch-picks .fetch-picks-table {
  --table-font: var(--font-body) !important;
  width: 100%;
  table-layout: fixed;
  border-collapse: separate;
  border-spacing: 0;
  overflow: visible !important;
}

/* Prevent clipping in header */
.page-fetch-picks .fetch-picks-table thead,
.page-fetch-picks .fetch-picks-table thead tr,
.page-fetch-picks .fetch-picks-table thead th {
  overflow: visible !important;
}

/* Cell overflow */
.fetch-picks-table td {
  overflow: hidden !important;
  text-overflow: ellipsis !important;
  word-wrap: break-word !important;
  white-space: nowrap !important;
  max-width: 0 !important;
  padding: 0.5rem 0.75rem !important;
  box-sizing: border-box;
}

/* Compact columns */
.fetch-picks-table td:nth-child(2),
.fetch-picks-table td:nth-child(4),
.fetch-picks-table td:nth-child(6) {
  padding: 0.4rem 0.5rem !important;
  white-space: nowrap !important;
}

/* Allow Matchup and Pick to wrap */
.fetch-picks-table td:nth-child(3),
.fetch-picks-table td[data-label="Matchup"] {
  white-space: normal !important;
  overflow: hidden !important;
  text-overflow: ellipsis !important;
  word-wrap: break-word !important;
  max-width: 0 !important;
}

.fetch-picks-table td:nth-child(5),
.fetch-picks-table td[data-label="Pick"] {
  white-space: normal !important;
  overflow: hidden !important;
  text-overflow: ellipsis !important;
  word-wrap: break-word !important;
  max-width: 0 !important;
}

/* ==========================================================================
   COLUMN WIDTHS — 7 columns (total 100%)
   1: Date/Time 13%  2: League 8%  3: Matchup 28%  4: Segment 10%
   5: Pick 23%  6: Edge 10%  7: Fire 8%
   ========================================================================== */
.fetch-picks-table thead th:nth-child(1),
.fetch-picks-table tbody td:nth-child(1) {
  width: 13% !important; min-width: 120px !important; max-width: 13% !important;
}
.fetch-picks-table thead th:nth-child(2),
.fetch-picks-table tbody td:nth-child(2) {
  width: 8% !important; min-width: 65px !important; max-width: 8% !important;
}
.fetch-picks-table thead th:nth-child(3),
.fetch-picks-table tbody td:nth-child(3) {
  width: 28% !important; min-width: 220px !important; max-width: 28% !important;
}
.fetch-picks-table thead th:nth-child(4),
.fetch-picks-table tbody td:nth-child(4) {
  width: 10% !important; min-width: 80px !important; max-width: 10% !important;
}
.fetch-picks-table thead th:nth-child(5),
.fetch-picks-table tbody td:nth-child(5) {
  width: 23% !important; min-width: 160px !important; max-width: 23% !important;
}
.fetch-picks-table thead th:nth-child(6),
.fetch-picks-table tbody td:nth-child(6) {
  width: 10% !important; min-width: 75px !important; max-width: 10% !important;
}
.fetch-picks-table thead th:nth-child(7),
.fetch-picks-table tbody td:nth-child(7) {
  width: 8% !important; min-width: 70px !important; max-width: 8% !important;
}

/* ==========================================================================
   TABLE HEADERS — transparent green gradient + sticky
   ========================================================================== */
body.page-fetch-picks .picks-table thead th,
.fetch-picks-table thead th {
  background: linear-gradient(
    180deg,
    rgba(0, 214, 137, 0.06) 0%,
    rgba(0, 214, 137, 0.03) 45%,
    rgba(8, 12, 22, 0.18) 100%
  );
  background-color: rgba(8, 12, 22, 0.15);
  border-bottom: 1px solid rgba(0, 214, 137, 0.2);
  border-right: 1px solid rgba(0, 214, 137, 0.08);
  padding: 0.75rem 0.75rem;
  text-align: center;
  font-family: var(--font-display, "Oswald", sans-serif);
  font-weight: 700;
  font-size: 0.85rem;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #f4fbff;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.35);
  line-height: 1.2;
  position: relative;
}

.fetch-picks-table thead th:first-child { border-top-left-radius: 8px; }
.fetch-picks-table thead th:last-child  { border-top-right-radius: 8px; border-right: none; }

/* Sticky header */
.page-fetch-picks .fetch-picks-table thead,
.page-fetch-picks .picks-table thead {
  position: sticky !important;
  top: 0;
  z-index: 110;
  background: transparent !important;
  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px);
}

.page-fetch-picks .fetch-picks-table thead th,
.page-fetch-picks .picks-table thead th {
  position: relative;
  text-align: center;
  padding: 8px 6px !important;
  background-clip: padding-box;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.45);
  vertical-align: middle;
}

.page-fetch-picks .fetch-picks-table th {
  position: relative !important;
  padding-right: 0 !important;
}

.page-fetch-picks .fetch-picks-table .th-header-shell {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  gap: 0;
}

.page-fetch-picks .fetch-picks-table .th-sort-btn {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100%;
  padding: 2px 4px;
  gap: 0;
  min-height: unset;
}

.page-fetch-picks .fetch-picks-table .header-main-text {
  display: block;
  font-size: 0.85rem;
  font-weight: 700;
  line-height: 1.2;
}

.page-fetch-picks .fetch-picks-table .header-subtext {
  display: block;
  font-size: 0.65rem;
  font-weight: 500;
  color: rgba(200, 215, 225, 0.7);
  text-transform: none;
  letter-spacing: 0.02em;
  margin-top: 2px;
  line-height: 1;
}

/* Sort icon */
.page-fetch-picks .fetch-picks-table .sort-icon,
.page-fetch-picks .picks-table .sort-icon {
  position: absolute;
  right: 2px;
  top: 50%;
  transform: translateY(-50%);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 7px; line-height: 1; font-weight: bold;
  color: rgba(34, 197, 94, 0.5);
  opacity: 0.7;
  margin: 0; pointer-events: none;
  background: none; border: none; box-shadow: none; border-radius: 0;
  transition: opacity 150ms ease, color 150ms ease;
}

.page-fetch-picks .fetch-picks-table th:hover .sort-icon { opacity: 1; color: rgba(74, 222, 128, 0.9); }

.page-fetch-picks .fetch-picks-table th.sorted-asc .sort-icon,
.page-fetch-picks .fetch-picks-table th.sorted-desc .sort-icon {
  opacity: 1; color: rgba(34, 197, 94, 1);
}

/* ==========================================================================
   TABLE BODY
   ========================================================================== */
.page-fetch-picks .picks-table tbody {
  position: relative;
  z-index: 10;
  background: rgba(8, 12, 22, 0.52);
}

body.page-fetch-picks .picks-table tbody tr,
.fetch-picks-table tbody tr {
  border-bottom: 1px solid rgba(80, 100, 130, 0.12);
  transition: background-color 0.2s ease;
  background: transparent;
}

body.page-fetch-picks .picks-table tbody tr:nth-child(odd),
.fetch-picks-table tbody tr:nth-child(odd) {
  background: rgba(4, 12, 24, 0.42);
}

body.page-fetch-picks .picks-table tbody tr:nth-child(even),
.fetch-picks-table tbody tr:nth-child(even) {
  background: rgba(30, 55, 85, 0.28);
}

body.page-fetch-picks .picks-table tbody tr:hover,
.fetch-picks-table tbody tr:hover {
  background: rgba(40, 60, 90, 0.48);
}

.fetch-picks-table tbody td {
  padding: 0.4rem 0.4rem;
  color: rgba(232, 240, 242, 0.95);
  vertical-align: middle;
  font-family: var(--font-body, "Inter", sans-serif);
  font-weight: 500;
  font-size: 0.825rem;
  line-height: 1.2;
  letter-spacing: 0.01em;
  white-space: nowrap;
