body.page-fetch-picks .ft-coming-soon {
  opacity: 0.35;
  cursor: not-allowed !important;
  pointer-events: none;
}

body.page-fetch-picks .coming-soon-label {
  font-style: italic;
}

/* ==========================================================================
   EMPTY STATE
   ========================================================================== */
body.page-fetch-picks .empty-state-cell {
  padding: 60px 20px !important;
  text-align: center;
}

body.page-fetch-picks .empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  color: var(--neutral-400);
}

body.page-fetch-picks .empty-icon {
  font-size: 2.5rem;
}

body.page-fetch-picks .empty-message {
  font-family: var(--font-body, "Inter", sans-serif);
  font-size: 0.9rem;
  color: var(--neutral-400);
  max-width: 360px;
  line-height: 1.5;
}

/* ==========================================================================
   LOADING SKELETON
   ========================================================================== */
body.page-fetch-picks .skeleton-row td {
  position: relative;
  overflow: hidden !important;
}

body.page-fetch-picks .skeleton-row td::after {
  content: "";
  display: block;
  height: 14px;
  width: 60%;
  border-radius: 4px;
  background: linear-gradient(
    90deg,
    rgba(100, 116, 139, 0.1) 25%,
    rgba(100, 116, 139, 0.2) 50%,
    rgba(100, 116, 139, 0.1) 75%
  );
  background-size: 200% 100%;
  animation: skeletonShimmer 1.5s ease-in-out infinite;
}

@keyframes skeletonShimmer {
  0%   { background-position: 200% 0; }
  100% { background-position: -200% 0; }
}

/* ==========================================================================
   FETCH-IN-PROGRESS SPINNER
   ========================================================================== */
body.page-fetch-picks .ft-fetch-league-btn.fetching {
  pointer-events: none;
  opacity: 0.6;
}

body.page-fetch-picks .ft-fetch-league-btn.fetching .refresh-icon {
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to   { transform: rotate(360deg); }
}

/* ==========================================================================
   PICKS COUNT BADGE
   ========================================================================== */
body.page-fetch-picks .picks-count-badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 20px;
  height: 20px;
  padding: 0 6px;
  border-radius: 10px;
  font-family: var(--font-display, "Oswald", sans-serif);
  font-weight: 700;
  font-size: 0.7rem;
  color: #fff;
  background: linear-gradient(135deg, var(--emerald-600), var(--emerald-500));
  margin-left: 6px;
}

/* ==========================================================================
   RESPONSIVE — TABLET
   ========================================================================== */
@media (max-width: 1024px) {
  body.page-fetch-picks .table-container,
  .page-fetch-picks .fetch-picks-table-wrapper {
    margin-left: 0.25in;
    margin-right: 0.25in;
    width: calc(100% - 0.5in);
  }
}

/* ==========================================================================
   RESPONSIVE — MOBILE (<767px)
   Table transforms to cards via mobile-responsive-v2.css (core bundle)
   ========================================================================== */
@media (max-width: 767px) {
  body.page-fetch-picks .table-container,
  .page-fetch-picks .fetch-picks-table-wrapper {
    margin-left: 8px;
    margin-right: 8px;
    width: calc(100% - 16px);
  }

  /* Card layout data labels for mobile */
  .fetch-picks-table td:nth-child(1)::before { content: "Date/Time"; }
  .fetch-picks-table td:nth-child(2)::before { content: "League"; }
  .fetch-picks-table td:nth-child(3)::before { content: "Matchup"; }
  .fetch-picks-table td:nth-child(4)::before { content: "Segment"; }
  .fetch-picks-table td:nth-child(5)::before { content: "Pick"; }
  .fetch-picks-table td:nth-child(6)::before { content: "Edge"; }
  .fetch-picks-table td:nth-child(7)::before { content: "Fire"; }

  /* Reset column constraints for card layout */
  .fetch-picks-table thead th,
  .fetch-picks-table tbody td {
    width: auto !important;
    min-width: 0 !important;
    max-width: none !important;
  }
}


