.table-container::-webkit-scrollbar-track:hover {
  background: linear-gradient(
    180deg,
    rgba(8, 16, 28, 0.7) 0%,
    rgba(2, 6, 23, 0.9) 50%,
    rgba(8, 16, 28, 0.7) 100%
  );
}

.table-container::-webkit-scrollbar-thumb {
  background: linear-gradient(
    180deg,
    rgba(var(--color-primary-bright-rgb), 0.5) 0%,
